<template>
  <div id="train-algorithm-rf">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <!-- 测试集占比 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>测试集占比：</span>
          <el-input-number v-model="train_algorithm_rf_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- n_estimators -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>n_estimators：</span>
          <el-input-number v-model="train_algorithm_rf_setting.paras[0].n_estimators" :min="0"
            :step="5"></el-input-number>
        </div>
        <!-- max_depth -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>max_depth：</span>
          <el-input-number v-model="train_algorithm_rf_setting.paras[0].max_depth" :min="1" :step="1"></el-input-number>
        </div>
        <!-- criterion -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>criterion：</span>
          <el-select v-model="train_algorithm_rf_setting.paras[0].criterion" placeholder="criterion">
            <el-option v-for="criterion in criterions" :key="criterion.value" :label="criterion.label"
              :value="criterion.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="测试集占比">
            训练模型时会将内置数据拆分为两部分，一部分是训练集，用来训练模型，一部分是测试集，用来测试训练的模型的效果。测试集占比就是测试集的数据量占全部内置数据的比值
          </el-collapse-item>
          <el-collapse-item title="n_estimators">
            森林中树的数量
          </el-collapse-item>
          <el-collapse-item title="max_depth">
            树的最大深度
          </el-collapse-item>
          <el-collapse-item title="criterion">
            <p>
              用于衡量一个分裂的质量。
            </p>
            <p>
              “squared_error”表示均方误差，它等于方差减少作为特征选择标准，并使用每个终端节点的平均值来最小化L2损失;
            </p>
            <p>
              “friedman_mse”使用均方误差和弗里德曼改进分数来最小化潜在分裂;
            </p>
            <p>
              “absolute_error”表示平均绝对误差，它使用每个终端节点的中值来最小化L1损失;
            </p>
            <p>
              “poisson”使用泊松偏差的减少来寻找分裂。
            </p>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      <p>
        随机森林（Random
        Forest，RF）是一种机器集成学习算法，由Breiman在2001年首次提出。集成学习通过构建多个学习器来完成指定的学习任务，随机森林以决策树作为基学习器，并在决策树的基础上引入了随机属性选择。将样本Y输入到随机森林模型后，模型会从输入的样本数据中有放回的随机抽取数据，训练N个决策树模型，每棵决策树会根据自身的属性独立的做出分类预测或者回归预测，随后将每棵决策树的结果放在一起进行投票汇总。对于分类问题，得票数最高的类别即为预测类别；对于回归问题，所有决策树预测结果的平均值即为预测数值。随机森林的分类决策过程可表示为：
      </p>
      <p>
        <Latex>H(x)=\underset{y \in Y}{\arg \max } \sum_{i=1}^{N} I\left[h_{i}(x)=y\right]</Latex>
      </p>
      <p>
        H(x)——分类结果；
      </p>
      <p>
        h(i)——第i个决策树模型；
      </p>
      <p>
        I——度量函数；
      </p>
      <p>
        N——决策树数量；
      </p>
      <p>
        y——目标变量
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_rf_result).length > 0" class="model-result">
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_rf_result).length > 0" :data="[train_algorithm_rf_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_rf_result).length > 0" :data="[train_algorithm_rf_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import Latex from "@/components/Latex.vue";
import { draw_lineplot } from "@/utils/draw";



export default {
  name: 'TrainAlgorithmRf',
  components: {
    Latex
  },
  data() {
    return {
      // criterion
      criterions: [{
        value: "squared_error",
        label: 'squared_error'
      },
      {
        value: "absolute_error",
        label: 'absolute_error'
      },
      {
        value: "friedman_mse",
        label: 'friedman_mse'
      },
      {
        value: "poisson",
        label: 'poisson'
      },
      ],
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_rf_setting: { // 随机森林回归参数
      get() {
        return this.$store.state.train_algorithm_rf_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_rf_setting', newVal);
      }
    },
    train_algorithm_rf_result: { // 随机森林回归训练结果
      get() {
        return this.$store.state.train_algorithm_rf_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_rf_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_rf_setting
    train_algorithm_rf_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_rf_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_rf_setting.data = this.train_process_result;
        train(this.train_algorithm_rf_setting).then(result => {
          this.train_algorithm_rf_result = result;
          // 绘制折线图
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_rf_result).length > 0) {
        const train_algorithm_rf_setting = this.train_algorithm_rf_setting;
        const train_algorithm_rf_result = this.train_algorithm_rf_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_rf_setting,
          train_algorithm_result: train_algorithm_rf_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 绘制折线图
      draw_lineplot("lr-bsr-lineplot", this.train_algorithm_rf_result, "bsr");
      draw_lineplot("lr-sbr-lineplot", this.train_algorithm_rf_result, "sbr");
      draw_lineplot("lr-d-lineplot", this.train_algorithm_rf_result, "d");
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_rf_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-rf {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
