const router = new VueRouter({
  routes
});

// 路由守卫
router.beforeEach((to, from, next) => {
  if (getCookie("mlp")) {
    if (to.path === "/login") {// 存在cookie时不能前往/login
      next(from.path);
    }
    next();
  } else {
    if (to.path === "/login") {
      next();
    } else {
      next('/login');
    }
  }
});

export default router;
