//引入二次封装的axios
import requests from "./axios";
// 接口请求函数
export const login = (params) => requests({ url: '/login ', method: 'post', data: params });
export const process_user = (params) => requests({ url: '/process/user', method: 'post', data: params });
export const process_inside = (params) => requests({ url: '/process/inside ', method: 'post', data: params });
export const train = (params) => requests({ url: '/train ', method: 'post', data: params });
export const predict = (params) => requests({ url: '/predict ', method: 'post', data: params });