<template>
  <div id="process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>选择井间连通模式：</span>
          <el-select v-model="train_mode" placeholder="井间连通模式">
            <el-option v-for="mode in train_modes" :key="mode.value" :label="mode.label" :value="mode.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 地质背景 -->
    <el-card shadow="hover">
      <div slot="header">
        <b>
          地质背景
        </b>
      </div>
      <el-row :gutter="12">
        <el-col :span="12">
          <el-card shadow="never">
            <div slot="header">
              油藏剖面图
            </div>
            <el-image class="image" :src="src_1" :preview-src-list="[src_1]" fit="contain">
            </el-image>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card shadow="never">
            <div slot="header">
              连通模式图
            </div>
            <el-image class="image" :src="src_2" :preview-src-list="[src_2]" fit="contain">
            </el-image>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
    <!-- 内置数据统计 -->
    <el-card shadow="hover">
      <div slot="header">
        <b>
          内置数据信息
        </b>
        <div>
          <div style="margin-top: 20px;margin-bottom: 20px;">
            共{{ mode_info.size.rows }} 行，{{ mode_info.size.columns }} 列
          </div>
          <el-tabs stretch>
            <el-tab-pane v-for="(feature, index) in mode_info.features" :label="index">
              <el-table :data="[feature]" style="width: 100%" stripe>
                <el-table-column v-for="(column, index) in Object.keys(feature)" :key="column" :prop="column"
                  :label="columns_info[index]">
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </div>

      </div>
    </el-card>
    <!-- 内置数据详情 -->
    <el-card shadow="hover">
      <div slot="header">
        <b>
          内置数据详情
        </b>
      </div>
      <el-table :data="mode_data" style="width: 100%" stripe>
        <el-table-column type="index" width="50" fixed>
        </el-table-column>
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>

  </div>
</template>

<script>
export default {
  name: 'TrainMode',
  data() {
    return {
      // 井间连通模式
      train_modes: [
        {
          value: 1,
          label: '模式1'
        }, {
          value: 2,
          label: '模式2'
        }, {
          value: 3,
          label: '模式3'
        }, {
          value: 4,
          label: '模式4'
        }, {
          value: 5,
          label: '模式5'
        }
      ],
      // 内置数据信息表头
      columns_info: ["最大值", "最小值", "均值", "中位数", "众数", "方差", "标准差"],
      // 绘制箱线图
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "BSR", "SBR", "D"],
    };
  },
  computed: {
    train_mode: { // 井间连通模式
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    src_1() {
      return require(`@/assets/imgs/mode_${this.train_mode}_1.png`);
    },
    src_2() {
      return require(`@/assets/imgs/mode_${this.train_mode}_2.png`);
    },
    mode_info() {
      return require(`@/assets/data/mode_${this.train_mode}_info.json`);
    },
    mode_data() {
      return require(`@/assets/data/mode_${this.train_mode}.json`);
    }
  },
  methods: {
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#process-abnormal {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  // 保存的模型
  .el-collapse-item__header {
    font-weight: 18px !important;
    font-weight: bold !important;
  }

}
</style>