<template>
  <el-form :model="login_form" :rules="login_rules" autocomplete="on" label-position="left" style="height: 100%;">
    <div id="login">
      <div class="login-info">
        <div class="login-logo">
          <img src="@/assets/imgs/logo_upc.png" alt="中国石油大学（华东）">
          <img src="@/assets/imgs/logo-cnpc.png" alt="中石油">
        </div>
        <div class="login-title">
          <h1>基于机器学习砂潜叠置储层连通性分析平台</h1>
        </div>
      </div>
      <div class="login-form">
        <el-form-item prop="username" label="用户名">
          <el-input v-model="login_form.username" placeholder="Username" type="text" tabindex="1" autocomplete="on" />
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input v-model="login_form.password" type="password" placeholder="Password" tabindex="2"
            autocomplete="off" />
        </el-form-item>
        <div class="login-btn">
          <el-button :loading="loading" type="primary" @click.native.prevent="login">登录</el-button>
        </div>
      </div>
    </div>
  </el-form>
</template>

<script>
import { login } from "@/api";
import router from "@/router";
import { setCookie } from "@/utils/cookie";
export default {
  name: 'Login',
  data() {
    return {
      login_form: {
        username: "",
        password: ""
      },
      login_rules: {
        username: [{ required: true, trigger: 'blur' }],
        password: [{ required: true, trigger: 'blur' }]
      },
      loading: false
    };
  },
  methods: {
    login() {
      if (this.login_form.username.length > 0 && this.login_form.password.length > 0) {
        this.loading = true;
        login({ username: this.login_form.username, password: this.login_form.password }).then(result => {
          if (result) {
            setCookie("mlp", 1296000);
            this.loading = false;
            router.replace({ path: '/' });
          }
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
#login {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .login-info {
    display: flex;
    flex-direction: column;
    align-items: center;

    .login-logo {
      height: 80px;

      img {
        height: 100%;
      }
    }
  }

  .login-title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .login-form {
    width: 500px;

    .login-btn {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  }
}</style>