<template>
  <div id="train-algorithm-xgb">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <!-- 测试集占比 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>测试集占比：</span>
          <el-input-number v-model="train_algorithm_xgb_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- learning_rate -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>learning_rate：</span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].learning_rate" :min="0.1"
            :step="0.1"></el-input-number>
        </div>
        <!-- max_depth -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>max_depth：</span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].max_depth" :min="1" :step="1"></el-input-number>
        </div>
        <!-- n_estimators -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>n_estimators：</span>
          <el-input-number v-model="train_algorithm_xgb_setting.paras[0].n_estimators" :min="1"
            :step="1"></el-input-number>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="测试集占比">
            训练模型时会将内置数据拆分为两部分，一部分是训练集，用来训练模型，一部分是测试集，用来测试训练的模型的效果。测试集占比就是测试集的数据量占全部内置数据的比值
          </el-collapse-item>
          <el-collapse-item title="learning_rate">
            步长收缩用于更新，以防止过拟合
          </el-collapse-item>
          <el-collapse-item title="max_depth">
            树的最大深度，增加这个值将使模型更复杂，更容易过拟合
          </el-collapse-item>
          <el-collapse-item title="n_estimators">
            梯度提升树的数量
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      <p>
        XGBoost模型由陈天奇博士提出的一种基于决策树（GBDT）的集成机器学习算法，XGBoost的根本思想是训练多个弱决策树串联起来形成一个较强的决策树，每个决策树可能没有良好的分类效果，但是多个决策树肯定会得到更准确的结果。该模型适用于分类和回归问题，相比传统模型有着相当明显的优势：训练快、开销小、模型泛化误差小。它被看作是泛函的最优化，其目标函数为：
      </p>
      <p>
        <Latex>{{ formula_1 }}</Latex>
      </p>
      <p>
        <Latex>{{ formula_2 }}</Latex>——误差函数（loss函数）；
      </p>
      <p>
        <Latex>\Omega\left( f_{t} \right)</Latex>——正则化部分；
      </p>
      <p>
        误差函数中的<Latex>{{ formula_3 }}</Latex>为模型的输出项。正则化部分主要被用来降低模型的复杂度，防止过拟合，提高模型的泛化能力。其函数表达式具体为：
      </p>
      <p>
        <Latex>\Omega(f) = \gamma T + \frac{1}{2}\lambda\left\| \omega \right\|^{2}</Latex>
      </p>
      <p>
        T——树的节点个数；
      </p>
      <p>
        ω——节点数。
      </p>
      <p>
        XGBoosts算法与传统的提升算法不同的是，其损失函数通过二阶泰勒展开，即loss函数的二阶展开。
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_xgb_result).length > 0" class="model-result">
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_xgb_result).length > 0" :data="[train_algorithm_xgb_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_xgb_result).length > 0" :data="[train_algorithm_xgb_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import { draw_lineplot } from "@/utils/draw";
import Latex from "@/components/Latex.vue";


export default {
  name: 'TrainAlgorithmXgb',
  components: {
    Latex
  },
  data() {
    return {
      // 公式，为了解决公式中出现的{{}}和vue中的冲突，同时要将将\转义
      formula_1: "L(\\phi) = {\\sum_{i}{l\\left( {{\\hat{y}}_{i},y_{i}} \\right)}} + {\\sum_{k}\\Omega}\\left( f_{t} \\right)",
      formula_2: "l\\left( {{\\hat{y}}_{i},y_{i}} \\right)",
      formula_3: "{\\hat{y}}_{i}",
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_xgb_setting: { // XGBoost回归参数
      get() {
        return this.$store.state.train_algorithm_xgb_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_xgb_setting', newVal);
      }
    },
    train_algorithm_xgb_result: { // XGBoost回归训练结果
      get() {
        return this.$store.state.train_algorithm_xgb_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_xgb_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_xgb_setting
    train_algorithm_xgb_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_xgb_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_xgb_setting.data = this.train_process_result;
        train(this.train_algorithm_xgb_setting).then(result => {
          this.train_algorithm_xgb_result = result;
          // 绘制折线图
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_xgb_result).length > 0) {
        const train_algorithm_xgb_setting = this.train_algorithm_xgb_setting;
        const train_algorithm_xgb_result = this.train_algorithm_xgb_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_xgb_setting,
          train_algorithm_result: train_algorithm_xgb_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
    // 绘制折线图
    draw_lineplot_all() {
      // 绘制折线图
      draw_lineplot("lr-bsr-lineplot", this.train_algorithm_xgb_result, "bsr");
      draw_lineplot("lr-sbr-lineplot", this.train_algorithm_xgb_result, "sbr");
      draw_lineplot("lr-d-lineplot", this.train_algorithm_xgb_result, "d");
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_xgb_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-xgb {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
