<template>
  <div id="train-algorithm-bpn">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <!-- 测试集占比 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>测试集占比：</span>
          <el-input-number v-model="train_algorithm_bpn_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- hidden_layer_sizes_1 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>hidden_layer_sizes_1：</span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].hidden_layer_sizes_1" :min="1"
            :step="1"></el-input-number>
        </div>
        <!-- hidden_layer_sizes_2 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>hidden_layer_sizes_2：</span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].hidden_layer_sizes_2" :min="1"
            :step="1"></el-input-number>
        </div>
        <!-- random_state -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>random_state：</span>
          <el-input-number v-model="train_algorithm_bpn_setting.paras[0].random_state" :min="0"
            :step="1"></el-input-number>
        </div>
        <!-- activation -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>activation：</span>
          <el-select v-model="train_algorithm_bpn_setting.paras[0].activation" placeholder="activation">
            <el-option v-for="activation in activations" :key="activation.value" :label="activation.label"
              :value="activation.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="测试集占比">
            训练模型时会将内置数据拆分为两部分，一部分是训练集，用来训练模型，一部分是测试集，用来测试训练的模型的效果。测试集占比就是测试集的数据量占全部内置数据的比值
          </el-collapse-item>
          <el-collapse-item title="hidden_layer_sizes_1">
            第1个隐藏层中的神经元数量
          </el-collapse-item>
          <el-collapse-item title="hidden_layer_sizes_2">
            第2个隐藏层中的神经元数量
          </el-collapse-item>
          <el-collapse-item title="random_state">
            确定权重和偏差初始化的随机数生成，如果使用提前停止则训练测试分裂
          </el-collapse-item>
          <el-collapse-item title="activation">
            <p>
              隐藏层的激活函数。
            </p>
            <p>
              “identity”，无操作激活，用于实现线性瓶颈，返回f(x) = x
            </p>
            <p>
              “logistic”，逻辑sigmoid函数，返回f(x) = 1 / (1 + exp(-x))。
            </p>
            <p>
              “tanh”，双曲tan函数，返回f(x) = tanh(x)。
            </p>
            <p>
              “relu”，整流线性单位函数，返回f(x) = max(0, x)
            </p>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      <p>
        一个典型的BP神经网络模型包括输入层、隐层和输出层，如图所示。设所建立的模型为三层网络，输入层、隐层和输出层分别含有n、q、m个神经元，连接输入层和隐层的权值设为v，连接隐层和输出层的权值设为w。信息依次从输入层－隐层－输出层传送，F1（S型函数），F2（线性函数）分别为隐层传递函数。
      </p>
      <el-image class="image" :src="require('@/assets/imgs/train_bpn_1.png')" :preview-src-list="[require('@/assets/imgs/train_bpn_1.png')]"
        fit="contain">
      </el-image>
      <p>
        BP神经网络的运作分为学习期和工作期。学习期包括学习数据的正向传播和误差反向传播;工作期仅含预测数据的正向传播过程。若输出层的实际输出与期望值不同, 系统将自动计算其误差, 并将误差原路返回, 采用带动量的梯度下降法调整隐层权值。随着动态迭代次数的增加, 使误差函数值达到一定目标, 从而实现BP神经网络n维空间向m维空间的转换。
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_bpn_result).length > 0" class="model-result">
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_bpn_result).length > 0" :data="[train_algorithm_bpn_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_bpn_result).length > 0" :data="[train_algorithm_bpn_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import { draw_lineplot } from "@/utils/draw";


export default {
  name: 'TrainAlgorithmBpn',
  data() {
    return {
      // activation
      activations: [{
        value: "identity",
        label: 'identity'
      },
      {
        value: "logistic",
        label: 'logistic'
      },
      {
        value: "tanh",
        label: 'tanh'
      },
      {
        value: "relu",
        label: 'relu'
      },
      ],
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_bpn_setting: { // BP神经网络回归参数
      get() {
        return this.$store.state.train_algorithm_bpn_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_bpn_setting', newVal);
      }
    },
    train_algorithm_bpn_result: { // BP神经网络回归训练结果
      get() {
        return this.$store.state.train_algorithm_bpn_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_bpn_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_bpn_setting
    train_algorithm_bpn_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_bpn_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_bpn_setting.data = this.train_process_result;
        train(this.train_algorithm_bpn_setting).then(result => {
          this.train_algorithm_bpn_result = result;
          // 绘制折线图
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_bpn_result).length > 0) {
        const train_algorithm_bpn_setting = this.train_algorithm_bpn_setting;
        const train_algorithm_bpn_result = this.train_algorithm_bpn_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_bpn_setting,
          train_algorithm_result: train_algorithm_bpn_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 绘制折线图
      draw_lineplot("lr-bsr-lineplot", this.train_algorithm_bpn_result, "bsr");
      draw_lineplot("lr-sbr-lineplot", this.train_algorithm_bpn_result, "sbr");
      draw_lineplot("lr-d-lineplot", this.train_algorithm_bpn_result, "d");
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_bpn_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-bpn {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
