<template>
  <div id="process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>是否处理异常值：</span>
          <el-select v-model="process_setting.abnormal[0].state" placeholder="是否处理异常值">
            <el-option v-for="state in process_abnormal_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值检测方法：</span>
          <el-select v-model="process_setting.abnormal[0].detect" placeholder="异常值检测方法">
            <el-option v-for="detect in process_abnormal_detects" :key="detect.value" :label="detect.label"
              :value="detect.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值处理方法：</span>
          <el-select v-model="process_setting.abnormal[0].method" placeholder="异常值处理方法">
            <el-option v-for="method in process_abnormal_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      <div style="margin-bottom: 20px;">
        <div style="margin-bottom: 10px;">
          <b>异常值检测方法</b>
        </div>
        <el-collapse>
          <el-collapse-item title="IQR">
            <p>
              四分位数（Quartile）是统计学中分位数的一种，即把所有数值由小到大排列并分成四等份，处于三个分割点位置的数值就是四分位数。
            </p>
            <p>
              第一四分位数(Q1)，又称“较小四分位数”，等于该样本中所有数值由小到大排列后第25%的数字。
            </p>
            <p>
              第二四分位数(Q2)，又称“中位数”，等于该样本中所有数值由小到大排列后第50%的数字。
            </p>
            <p>
              第三四分位数(Q3)，又称“较大四分位数”，等于该样本中所有数值由小到大排列后第75%的数字。
            </p>
            <p>
              第三四分位数与第一四分位数的差距又称四分位距（InterQuartile Range, IQR）。
            </p>
            <p>
              IQR方法认为大于第四分位数与小于第一四分位数的数据为异常值。
            </p>
          </el-collapse-item>
          <el-collapse-item title="Z-score">
            <p>
              Z分数（Z-score），也叫标准分数（standard score）是一个数与平均数的差再除以标准差的过程。
            </p>
            <p>
              Z分数表示一个给定分数距离平均数多少个标准差，在平均数之上的分数会得到一个正的标准分数，在平均数之下的分数会得到一个负的标准分数。 Z分数是一种可以看出某分数在分布中相对位置的方法。
            </p>
            <p>
              Z-score方法认为分布在距离平均数三个标准差之外的数据为异常值。
            </p>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div>
        <div style="margin-bottom: 10px;">
          <b>异常值处理方法</b>
        </div>
        <el-collapse>
          <!-- <el-collapse-item title="删除">
            直接删除异常值所在的那行数据
          </el-collapse-item> -->
          <el-collapse-item title="均值替换">
            将异常值替换为异常值所在的那列的均值
          </el-collapse-item>
          <el-collapse-item title="中位数替换">
            将异常值替换为异常值所在的那列的中位数
          </el-collapse-item>
          <el-collapse-item title="众数替换">
            将异常值替换为异常值所在的那列的众数
          </el-collapse-item>
          <el-collapse-item title="边界值替换">
            <p>
              异常值检测方法为IQR时，将大于第四分位数的数据替换为第四分位数，小于第一分位数的数据替换为第一分位数。
            </p>
            <p>
              异常值检测方法为Z-score时，将大于平均数+三个标准差的数据替换为平均数+三个标准差处的数据，小于平均数-三个标准差的数据替换为平均数-三个标准差处的数据。
            </p>
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <!-- <el-card v-if="process_input_data.length > 0" class="condition" shadow="hover">
      <div slot="header">
        <b>
          异常值情况
        </b>
      </div>
      <div id="process-boxplot">
        11
      </div>
    </el-card> -->
  </div>
</template>

<script>
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'ProcessAbnormal',
  data() {
    return {
      // 是否处理异常值
      process_abnormal_states: [
        {
          value: 0,
          label: '否'
        }, {
          value: 1,
          label: '是'
        }
      ],
      // 异常值检测方法
      process_abnormal_detects: [
        {
          value: 1,
          label: 'IQR'
        }, {
          value: 2,
          label: 'Z-score'
        }
      ],
      // 异常值处理方法
      process_abnormal_methods: [
        // {
        //   value: 1,
        //   label: '删除'
        // }, 
        {
          value: 2,
          label: '均值替换'
        }, {
          value: 3,
          label: '中位数替换'
        }, {
          value: 4,
          label: '众数替换'
        }, {
          value: 5,
          label: '边界值替换'
        }
      ],
      // 绘制箱线图
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
    };
  },
  computed: {
    process_input_data: { // 导入的数据，用于绘制箱线图
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 绘制箱线图
    draw_boxplot() {
      // 整理每列数据
      let data_coloum = {};
      this.columns.map(column => data_coloum[column] = []);
      this.process_input_data.map(row_data => {
        for (let coloum in data_coloum) {
          if (typeof row_data[coloum] != 'undefined') {
            data_coloum[coloum].push(row_data[coloum]);
          }
        }
      });
      // 构造箱线图数据
      let data = [];
      this.columns.map(column => {
        data.push({
          y: data_coloum[column],
          type: 'box',
          name: column,
          marker: {
            color: 'rgb(8,81,156)',
            outliercolor: 'rgba(219, 64, 82, 0.6)',
            line: {
              outliercolor: 'rgba(219, 64, 82, 1.0)',
              outlierwidth: 2
            }
          },
          boxpoints: 'suspectedoutliers'
        });
      });
      const layout = {
        // title: '箱线图'
        autosize: true, // 自动跳转大小
        showlegend: false, // 隐藏图例
      };
      Plotly.newPlot('process-boxplot', data, layout);
      // 保存到store
      this.process_boxplot = data;
    }
  },
  mounted() {
    // 绘制箱线图。测试发现由于数据差距非常大，基本上都压成一条直线没有高度了
    // if (this.process_input_data.length > 0) {
    //   this.draw_boxplot();
    // }
  }
};
</script>

<style lang="scss" scoped>
#process-abnormal {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>