<template>
  <div id="train-save">
    <el-alert v-if="!train_save.length > 0" title="没有保存的模型" type="warning" show-icon :closable="false">
    </el-alert>
    <el-collapse v-else>
      <TrainSaveItem v-for=" ( model, index) in train_save " :index="index + 1" :model="model" />
    </el-collapse>
  </div>
</template>

<script>
import TrainSaveItem from "@/components/TrainSaveItem.vue";


export default {
  name: 'TrainSave',
  components: {
    TrainSaveItem
  },
  data() {
    return {
    };
  },
  computed: {
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  methods: {
  },
  mounted() {
  }
};
</script>

