<template>
  <div id="train-algorithm-lr">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>测试集占比：</span>
          <el-input-number v-model="train_algorithm_lr_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- 还是默认截距不为0好了 -->
        <!-- <div class="setting-method" style="margin-right: 20px;">
          <span>截距不为0：</span>
          <el-select v-model="train_algorithm_lr_setting.paras[0].fit_intercept" placeholder="截距为0">
            <el-option v-for="method in fit_intercept_state" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div> -->
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="测试集占比">
            训练模型时会将内置数据拆分为两部分，一部分是训练集，用来训练模型，一部分是测试集，用来测试训练的模型的效果。测试集占比就是测试集的数据量占全部内置数据的比值
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      <p>
        在线性回归中，如果自变量及因变量各有一个，并且可用一条直线近似表示自变量与因变量之间的关系，则称该线性回归为一元线性回归模型：
      </p>
      <p>
        <Latex>y_{t}=m+n x_{t}</Latex>
      </p>
      <p>
        x(t)——t期自变量的值；
      </p>
      <p>
        m、n——模型方程的系数。
      </p>
      <p>
        如果存在两个或两个以上的自变量则称该线性回归为多元线性回归模型：
      </p>
      <p>
        <Latex>y=p_{0}+p_{1} x_{1}+p_{2} x_{2}+\cdots+p_{k} x_{k}+e</Latex>
      </p>
      <p>
        p(0)——常数项；
      </p>
      <p>
        <Latex>p_{1}、p_{2}\cdots p_{k}</Latex>——回归系数。
      </p>
      <p>
        值得注意的是，一元线性回归分析其实就是线性回归分析的一个特例，众多的线性回归问题都可以先从一元线性回归开始研究分析进而简化，并且有两个自变量时，回归模型是空间中的一个平面，有更多自变量时，回归模型将是更高维的。
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_lr_result).length > 0" class="model-result">
        <!-- 公式 -->
        <div class="formula">
          <Latex>{{ bsr_katex }}</Latex>
        </div>
        <div class="formula">
          <Latex>{{ sbr_katex }}</Latex>
        </div>
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot" style="width: 100%;"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import Latex from "@/components/Latex.vue";
import { draw_lineplot } from "@/utils/draw";


export default {
  name: 'TrainAlgorithmLr',
  components: {
    Latex
  },
  data() {
    return {
      // 选择导入数据方法
      fit_intercept_state: [{
        value: "true",
        label: '是'
      }, {
        value: "false",
        label: '否'
      }],
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_lr_setting: { // 最小二乘法线性回归参数
      get() {
        return this.$store.state.train_algorithm_lr_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_setting', newVal);
      }
    },
    train_algorithm_lr_result: { // 最小二乘法线性回归训练结果
      get() {
        return this.$store.state.train_algorithm_lr_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
    // 公式字符串
    bsr_katex() {
      const train_algorithm_lr_result = this.$store.state.train_algorithm_lr_result;
      if (Object.keys(train_algorithm_lr_result).length > 0) {
        let coef = train_algorithm_lr_result.bsr.results.coef;
        let intercept = train_algorithm_lr_result.bsr.results.intercept;
        // 生成latex代码
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // 正数前显示+号
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "BSR=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
    sbr_katex() {
      const train_algorithm_lr_result = this.$store.state.train_algorithm_lr_result;
      if (Object.keys(train_algorithm_lr_result).length > 0) {
        let coef = train_algorithm_lr_result.sbr.results.coef;
        let intercept = train_algorithm_lr_result.sbr.results.intercept;
        // 生成latex代码
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // 正数前显示+号
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "SBR=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_lr_setting
    train_algorithm_lr_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_lr_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_lr_setting.data = this.train_process_result;
        train(this.train_algorithm_lr_setting).then(result => {
          console.log(result);
          this.train_algorithm_lr_result = result;
          // 绘制折线图
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 绘制折线图
      draw_lineplot("lr-bsr-lineplot", this.train_algorithm_lr_result, "bsr");
      draw_lineplot("lr-sbr-lineplot", this.train_algorithm_lr_result, "sbr");
      draw_lineplot("lr-d-lineplot", this.train_algorithm_lr_result, "d");
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_lr_result).length > 0) {
        const train_algorithm_lr_setting = this.train_algorithm_lr_setting;
        const train_algorithm_lr_result = this.train_algorithm_lr_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_lr_setting,
          train_algorithm_result: train_algorithm_lr_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_lr_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-lr {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }
}
</style>
