import Plotly from 'plotly.js-dist-min';

// 绘制折线图
export function draw_lineplot(id,result, tag) {
  // 直线图标题
  let title = "";
  if (tag == "bsr") {
    title = "BSR";
  }
  if (tag == "sbr") {
    title = "SBR";
  }
  if (tag == "d") {
    title = "D";
  }
  // 绘制折线图所需的数据
  const y_test = {
    x: Object.keys(result[tag].y_test),
    y: result[tag].y_test,
    type: 'lines+markers',
    name: "真实值"
  };
  const y_pred = {
    x: Object.keys(result[tag].y_pred),
    y: result[tag].y_pred,
    type: 'lines+markers',
    name: "预测值"
  };
  const data = [y_test, y_pred];
  const layout = {
    title
    // xaxis: {
    //   title: ''
    // }
  };
  const config = { responsive: true };
  Plotly.newPlot(id, data, layout, config);
}