<template>
  <el-collapse-item :title="'模型' + index + ' - ' + algorithm" :name="index">
    <!-- <h3>{{ algorithm }}</h3> -->
    <h4>
      模型参数：
    </h4>
    <el-descriptions direction="vertical" style="width: 100%;margin-bottom: 30px;" border>
      <el-descriptions-item label="test_size">{{ model.train_algorithm_setting.test_size }}</el-descriptions-item>
      <el-descriptions-item v-for="para in model.train_algorithm_setting.paras[0]" label="learning_rate">{{ para
      }}</el-descriptions-item>
    </el-descriptions>
    <div class="model-result">
      <!-- 显示线性回归里的系数和截距 -->
      <div v-if="setting.algorithm == 1" class="formulas">
        <div class="formula">
          <Latex>{{ bsr_katex }}</Latex>
        </div>
        <div class="formula">
          <Latex>{{ sbr_katex }}</Latex>
        </div>
      </div>
      <!-- 误差统计 -->
      <h4>
        BSR：
      </h4>
      <el-table v-if="Object.keys(result).length > 0" :data="[result.bsr.error]" style="width: 100%;margin-bottom: 30px;"
        stripe>
        <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
      <h4>
        SBR：
      </h4>
      <el-table v-if="Object.keys(result).length > 0" :data="[result.sbr.error]" style="width: 100%;margin-bottom: 30px;"
        stripe>
        <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
      <!-- 折线图 -->
      <div :id="'lr-bsr-lineplot_' + index"></div>
      <div :id="'lr-sbr-lineplot_' + index"></div>
      <div :id="'lr-d-lineplot_' + index"></div>
    </div>
  </el-collapse-item>
</template>

<script>
import { draw_lineplot } from "@/utils/draw";
import Latex from "@/components/Latex.vue";


export default {
  name: 'TrainSaveItem',
  components: {
    Latex
  },
  props: ["index", "model"],
  data() {
    return {
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    algorithm() {
      if (this.model.train_algorithm_setting.algorithm == 1) {
        return "最小二乘法线性回归";
      } else if (this.model.train_algorithm_setting.algorithm == 2) {
        return "随机森林回归";
      } else if (this.model.train_algorithm_setting.algorithm == 3) {
        return "BP神经网络回归";
      } else if (this.model.train_algorithm_setting.algorithm == 4) {
        return "XGBoost回归";
      } else if (this.model.train_algorithm_setting.algorithm == 5) {
        return "LightGBM回归";
      }
    },
    setting() {
      return this.model.train_algorithm_setting;
    },
    result() {
      return this.model.train_algorithm_result;
    },
    // 公式字符串
    bsr_katex() {
      if (this.setting.algorithm == 1) {
        const train_algorithm_lr_result = this.result;
        let coef = train_algorithm_lr_result.bsr.results.coef;
        let intercept = train_algorithm_lr_result.bsr.results.intercept;
        // 生成latex代码
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // 正数前显示+号
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "BSR=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
    sbr_katex() {
      if (this.setting.algorithm == 1) {
        const train_algorithm_lr_result = this.result;
        let coef = train_algorithm_lr_result.sbr.results.coef;
        let intercept = train_algorithm_lr_result.sbr.results.intercept;
        // 生成latex代码
        coef = coef.map((v, i) => i == 0 ? v : (v < 0 ? "" : "+") + v); // 正数前显示+号
        intercept = (intercept < 0 ? "" : "+") + intercept;

        return "SBR=" + coef.map((v, i) => v + "\\times " + this.columns[i]).join("") + intercept;
      }
    },
  },
  methods: {
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.result).length > 0) {
      draw_lineplot("lr-bsr-lineplot_" + this.index, this.result, "bsr");
      draw_lineplot("lr-sbr-lineplot_" + this.index, this.result, "sbr");
      draw_lineplot("lr-d-lineplot_" + this.index, this.result, "d");
    }
  }
};
</script>

<style lang="scss" scoped>
#train-save-item {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
