<template>
  <div id="process-miss">
    <el-card shadow="hover">
      <div class="setting">
        <!-- 缺失值需要处理，因此直接默认处理 -->
        <!-- <div class="setting-method" style="margin-right: 20px;">
          <span>是否处理缺失值：</span>
          <el-select v-model="process_setting.miss[0].state" placeholder="是否处理异常值">
            <el-option v-for="state in process_miss_states" :key="state.value" :label="state.label" :value="state.value">
            </el-option>
          </el-select>
        </div> -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>缺失值处理方法：</span>
          <el-select v-model="process_setting.miss[0].method" placeholder="缺失值处理方法">
            <el-option v-for="method in process_miss_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 缺失值检测方法介绍 -->
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="均值填补">
            将缺失值所在的那列数据的均值填在缺失值的位置
          </el-collapse-item>
          <el-collapse-item title="中位数填补">
            将缺失值所在的那列数据的中位数填在缺失值的位置
          </el-collapse-item>
          <el-collapse-item title="众数填补">
            将缺失值所在的那列数据的众数填在缺失值的位置
          </el-collapse-item>
          <el-collapse-item title="线性填补">
            将根据缺失值所在的那列数据构建线性表达式计算出缺失值处的数据填在缺失值的位置
          </el-collapse-item>
          <el-collapse-item title="前值填补">
            将缺失值所在行的前一行的那列数据填在缺失值的位置
          </el-collapse-item>
          <el-collapse-item title="后值填补">
            将缺失值所在行的后一行的那列数据填在缺失值的位置
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <!-- 缺失值情况 -->
    <!-- <el-card v-if="process_input_data.length > 0" class="condition" shadow="hover">
      <div slot="header">
        <b>
          缺失值情况
        </b>
      </div>
    </el-card> -->
  </div>
</template>

<script>
export default {
  name: 'ProcessMiss',
  data() {
    return {
      // 缺失值处理方法
      process_miss_methods: [
        {
          value: 1,
          label: '均值填补'
        }, {
          value: 2,
          label: '中位数填补'
        }, {
          value: 3,
          label: '众数填补'
        }, {
          value: 4,
          label: '线性填补'
        }, {
          value: 5,
          label: '前值填补'
        }, {
          value: 6,
          label: '后值填补'
        }
      ],
      // columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
    };
  },
  computed: {
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
  },
  mounted() {
    // 缺失值统计
  }
};
</script>

<style lang="scss" scoped>
#process-miss {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>