<template>
  <div id="train-process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>是否处理异常值：</span>
          <el-select v-model="train_process_setting.abnormal[0].state" placeholder="是否处理异常值">
            <el-option v-for="state in process_abnormal_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值检测方法：</span>
          <el-select v-model="train_process_setting.abnormal[0].detect" placeholder="异常值检测方法">
            <el-option v-for="detect in process_abnormal_detects" :key="detect.value" :label="detect.label"
              :value="detect.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值处理方法：</span>
          <el-select v-model="train_process_setting.abnormal[0].method" placeholder="异常值处理方法">
            <el-option v-for="method in process_abnormal_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      <el-row :gutter="12">
        <el-col :span="12">
          异常值检测方法介绍
        </el-col>
        <el-col :span="12">
          异常值处理方法介绍
        </el-col>
      </el-row>
    </el-card>
    <!-- <el-card class="condition" shadow="hover">
      <div slot="header">
        <b>
          异常值情况
        </b>
      </div>
      <div id="train-process-boxplot">
        11
      </div>
    </el-card> -->
  </div>
</template>

<script>
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'TrainProcessAbnormal',
  data() {
    return {
      // 是否处理异常值
      process_abnormal_states: [
        {
          value: 0,
          label: '否'
        }, {
          value: 1,
          label: '是'
        }
      ],
      // 异常值检测方法
      process_abnormal_detects: [
        {
          value: 1,
          label: 'IQR'
        }, {
          value: 2,
          label: 'Z-score'
        }
      ],
      // 异常值处理方法
      process_abnormal_methods: [
        {
          value: 1,
          label: '删除'
        }, {
          value: 2,
          label: '均值替换'
        }, {
          value: 3,
          label: '中位数替换'
        }, {
          value: 4,
          label: '众数替换'
        }, {
          value: 5,
          label: '边界值替换'
        }
      ],
      // 绘制箱线图
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR","BSR","SBR","D"],
    };
  },
  computed: {
    train_process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.train_process_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_process_setting', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_process_setting
    train_process_setting: {
      handler(newVal, oldVal) {
        this.train_process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 绘制箱线图
    draw_boxplot() {
      // 整理每列数据
      let data_coloum = {};
      this.columns.map(column => data_coloum[column] = []);
      this.process_input_data.map(row_data => {
        for (let coloum in data_coloum) {
          if (typeof row_data[coloum] != 'undefined') {
            data_coloum[coloum].push(row_data[coloum]);
          }
        }
      });
      // 构造箱线图数据
      let data = [];
      this.columns.map(column => {
        data.push({
          y: data_coloum[column],
          type: 'box',
          name: column,
          marker: {
            color: 'rgb(8,81,156)',
            outliercolor: 'rgba(219, 64, 82, 0.6)',
            line: {
              outliercolor: 'rgba(219, 64, 82, 1.0)',
              outlierwidth: 2
            }
          },
          boxpoints: 'suspectedoutliers'
        });
      });
      const layout = {
        // title: '箱线图'
        autosize: true, // 自动跳转大小
        showlegend: false, // 隐藏图例
      };
      Plotly.newPlot('train-process-boxplot', data, layout);
      // 保存到store
      this.process_boxplot = data;
    }
  },
  mounted() {
    // 绘制箱线图。测试发现由于数据差距非常大，基本上都压成一条直线没有高度了
    //   this.draw_boxplot();
  }
};
</script>

<style lang="scss" scoped>
#train-process-abnormal {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>