<template>
  <div id="process-standard">
    <!-- 参数设置 -->
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>是否标准化处理：</span>
          <el-select v-model="process_setting.standard[0].state" placeholder="是否标准化处理">
            <el-option v-for="state in process_standard_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>标准化处理方法：</span>
          <el-select v-model="process_setting.standard[0].method" placeholder="标准化处理方法">
            <el-option v-for="method in process_standard_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 标准化处理方法介绍 -->
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="MinMaxScaler">
            对原始数据进行线性变换，将值映射到[0,1]之间
          </el-collapse-item>
          <el-collapse-item title="StandardScaler">
            去除平均值并将数据缩放到单位方差
          </el-collapse-item>
          <el-collapse-item title="MaxAbsScaler">
            与MinMaxScaler方法相似，但如果只存在正值，则范围为[0,1]。如果只存在负值，则范围为[-1,0]。如果同时存在负值和正值，则范围为[-1,1]
          </el-collapse-item>
          <el-collapse-item title="RobustScaler">
            RobustScaler和StandardScaler比较近似，但是它并不是用均值和方差来进行转换，而是使用中位数和四分位矩
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'ProcessStandard',
  data() {
    return {
      // 是否标准化处理
      process_standard_states: [
        {
          value: 0,
          label: '否'
        }, {
          value: 1,
          label: '是'
        }
      ],
      // 缺失值处理方法
      process_standard_methods: [
        {
          value: 1,
          label: 'MinMaxScaler'
        }, {
          value: 2,
          label: 'StandardScaler'
        }, {
          value: 3,
          label: 'MaxAbsScaler'
        }, {
          value: 4,
          label: 'RobustScaler'
        }
        // , {
        //   value: 5,
        //   label: 'Normalizer'
        // }
      ],
    };
  },
  computed: {
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#process-standard {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>