<template>
  <span ref="latex" class="katex" v-html="rawHTML" />
</template>

<script>
import katex from 'katex';
import 'katex/dist/katex.css';

export default {
  name: 'Latex',
  computed: {
    rawHTML() {
      return katex.renderToString(this.$slots.default[0].text, {
        throwOnError: false,
      });
    }
  }
};
</script>

<style scoped lang="scss"></style>




