<template>
  <div id="train-process-result">
    <el-alert title="注意：此处处理数据的方法和导入数据部分设置的一致" type="info" :closable="false">
    </el-alert>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <el-button type="primary" @click="train_process">点击处理</el-button>
      </div>
      <h3>处理结果：</h3>
      <el-table v-if="train_process_result.length > 0" :data="train_process_result" style="width: 100%" stripe>
        <el-table-column type="index" width="50" fixed>
        </el-table-column>
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { process_inside } from "@/api";


export default {
  name: 'TrainProcessResult',
  data() {
    return {
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "BSR", "SBR", "D"],
    };
  },
  computed: {
    process_input_data: { // 上传数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 上传数据部分处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
    mode_data() { // 内置数据
      return require(`@/assets/data/mode_${this.train_mode}.json`);
    },
    train_mode: { // 选择的井间连通模式
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    train_process_setting: { // 训练模型部分处理方法参数选择
      get() {
        return this.$store.state.train_process_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_process_setting', newVal);
      }
    },
    train_process_result: { // 处理后的数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_process_setting
    train_process_setting: {
      handler(newVal, oldVal) {
        this.train_process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 发送请求处理数据
    train_process() {
      // 判断是否导入数据
      if (this.process_input_data.length > 0) {
        // 添加导入的数据
        this.train_process_setting.data = this.mode_data;
        // 同步上传数据处理方法部分的参数
        this.train_process_setting.abnormal = this.process_setting.abnormal;
        this.train_process_setting.standard = this.process_setting.standard;
        // 发送请求
        process_inside(this.train_process_setting).then(result => {
          // console.log(JSON.parse(result));
          this.train_process_result = JSON.parse(result);
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先导入数据！',
          type: 'error'
        });
      }
    }
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#train-process-result {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>