<template>
  <div id="train-process-standard">
    <!-- 参数设置 -->
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>是否标准化处理：</span>
          <el-select v-model="train_process_setting.standard[0].state" placeholder="是否标准化处理">
            <el-option v-for="state in process_standard_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>标准化处理方法：</span>
          <el-select v-model="train_process_setting.standard[0].method" placeholder="标准化处理方法">
            <el-option v-for="method in process_standard_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 标准化处理方法介绍 -->
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      标准化处理方法介绍
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'TrainProcessStandard',
  data() {
    return {
      // 是否标准化处理
      process_standard_states: [
        {
          value: 0,
          label: '否'
        }, {
          value: 1,
          label: '是'
        }
      ],
      // 缺失值处理方法
      process_standard_methods: [
        {
          value: 1,
          label: 'Min-max'
        }, {
          value: 2,
          label: 'Z-score'
        }, {
          value: 3,
          label: 'MaxAbs'
        }, {
          value: 4,
          label: 'RobustScaler'
        }, {
          value: 5,
          label: '正则化'
        }
      ],
    };
  },
  computed: {
    train_process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.train_process_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_process_setting', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_process_setting
    train_process_setting: {
      handler(newVal, oldVal) {
        this.train_process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#train-process-standard {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>