<template>
  <el-container style="height: 100%; border: 1px solid #eee">
    <el-aside id="aside">
      <el-menu default-active="train_mode" @select="active">
        <el-menu-item index="train_mode">
          <i class="el-icon-finished"></i>选择连通模式
        </el-menu-item>
        <el-submenu index="process">
          <template slot="title"><i class="el-icon-files"></i>导入并处理数据</template>
          <el-menu-item index="process_input">
            导入数据
          </el-menu-item>
          <el-submenu index="process_set">
            <template slot="title">设置处理方法</template>
            <el-menu-item index="process_abnormal">
              异常值
            </el-menu-item>
            <el-menu-item index="process_miss">
              缺失值
            </el-menu-item>
            <el-menu-item index="process_standard">
              标准化
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="process_result">
            处理及结果
          </el-menu-item>
        </el-submenu>
        <el-submenu index="train">
          <template slot="title"><i class="el-icon-data-line"></i>训练模型</template>
          <!-- <el-menu-item index="train_process_result">
            处理模式数据
          </el-menu-item> -->
          <!-- <el-submenu index="train_process">
            <template slot="title">处理模式数据</template>
            <el-submenu index="train_process_set">
              <template slot="title">设置处理方法</template>
              <el-menu-item index="train_process_abnormal">
                异常值
              </el-menu-item>
              <el-menu-item index="train_process_standard">
                标准化
              </el-menu-item>
            </el-submenu>
            <el-menu-item index="train_process_result">
              处理及结果
            </el-menu-item>
          </el-submenu> -->
          <el-submenu index="train_algorithm">
            <template slot="title">选择算法训练</template>
            <el-menu-item index="train_algorithm_lr">
              多元线性回归
            </el-menu-item>
            <el-menu-item index="train_algorithm_rf">
              随机森林回归
            </el-menu-item>
            <el-menu-item index="train_algorithm_bpn">
              BP神经网络回归
            </el-menu-item>
            <el-menu-item index="train_algorithm_xgb">
              XGBoost回归
            </el-menu-item>
            <el-menu-item index="train_algorithm_lgb">
              LightGBM回归
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="train_save">
            查看保存的模型
          </el-menu-item>
        </el-submenu>
        <el-menu-item index="predict">
          <i class="el-icon-position"></i>连通性预测
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header id="header">
        <span class="header-title">
          基于机器学习砂潜叠置储层连通性分析平台
        </span>
      </el-header>
      <el-main id="main">
        <!-- 上传并处理数据 -->
        <div v-if="active_index == 'process_input'">
          <ProcessInput />
        </div>
        <div v-if="active_index == 'process_abnormal'">
          <ProcessAbnormal />
        </div>
        <div v-if="active_index == 'process_miss'">
          <ProcessMiss />
        </div>
        <div v-if="active_index == 'process_standard'">
          <ProcessStandard />
        </div>
        <div v-if="active_index == 'process_result'">
          <ProcessResult />
        </div>
        <!-- 训练模型 -->
        <div v-if="active_index == 'train_mode'">
          <TrainMode />
        </div>
        <div v-if="active_index == 'train_process_abnormal'">
          <TrainProcessAbnormal />
        </div>
        <div v-if="active_index == 'train_process_standard'">
          <TrainProcessStandard />
        </div>
        <div v-if="active_index == 'train_process_result'">
          <TrainProcessResult />
        </div>
        <div v-if="active_index == 'train_algorithm_lr'">
          <TrainAlgorithmLr />
        </div>
        <div v-if="active_index == 'train_algorithm_rf'">
