        </div>
        <div v-if="active_index == 'train_algorithm_xgb'">
          <TrainAlgorithmXgb />
        </div>
        <div v-if="active_index == 'train_algorithm_lgb'">
          <TrainAlgorithmLgb />
        </div>
        <div v-if="active_index == 'train_save'">
          <TrainSave />
        </div>
        <!-- 预测连通性 -->
        <div v-if="active_index == 'predict'">
          <Predict />
        </div>
      </el-main>
