<script>
// 上传并处理数据
import ProcessInput from '@/views/process/ProcessInput.vue';
import ProcessAbnormal from '@/views/process/ProcessAbnormal.vue';
import ProcessMiss from '@/views/process/ProcessMiss.vue';
import ProcessStandard from '@/views/process/ProcessStandard.vue';
import ProcessResult from '@/views/process/ProcessResult.vue';
// 训练模型
import TrainMode from '@/views/train/TrainMode.vue';
import TrainProcessAbnormal from '@/views/train/TrainProcessAbnormal.vue';
import TrainProcessStandard from '@/views/train/TrainProcessStandard.vue';
import TrainProcessResult from '@/views/train/TrainProcessResult.vue';
import TrainAlgorithmLr from '@/views/train/TrainAlgorithmLr.vue';
import TrainAlgorithmRf from '@/views/train/TrainAlgorithmRf.vue';
import TrainAlgorithmBpn from '@/views/train/TrainAlgorithmBpn.vue';
import TrainAlgorithmXgb from '@/views/train/TrainAlgorithmXgb.vue';
import TrainAlgorithmLgb from '@/views/train/TrainAlgorithmLgb.vue';
import TrainSave from '@/views/train/TrainSave.vue';
// 预测连通性
import Predict from '@/views/predict/Predict.vue';

export default {
  components: {
    ProcessInput,
    ProcessAbnormal,
    ProcessMiss,
    ProcessStandard,
    ProcessResult,
    TrainMode,
    TrainProcessAbnormal,
    TrainProcessStandard,
    TrainProcessResult,
    TrainAlgorithmLr,
    TrainAlgorithmRf,
    TrainAlgorithmBpn,
    TrainAlgorithmXgb,
    TrainAlgorithmLgb,
    TrainSave,
    Predict
  },
  data() {
    return {
      active_index: "train_mode", // 当前显示的目录项以及动态组件
    };
  },
  methods: {
    // 根据目录点击项切换动态组件
    active(index) {
      this.active_index = index;
    }
  }
};
</script>

<style lang="scss" scoped>
#app {
  height: 100vh;
  overflow: hidden;

  #aside {
    color: #333;
    width: 220px !important;
    overflow-x: hidden;

    // 路由
    a {
      color: inherit;
      text-decoration: none;
    }
  }

  #header {
    background-color: #f4f4f5;
    color: #333;
    line-height: 60px;
    text-align: right;
    font-size: 12px;

    .header-title {
      font-size: 18px;
      font-weight: bold;
    }
  }

  #main {}
}
</style>