<template>
  <div id="train-algorithm-lgb">
    <el-card shadow="hover">
      <div slot="header">
        <b>参数设置</b>
      </div>
      <div class="setting">
        <!-- 测试集占比 -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>测试集占比：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <!-- learning_rate -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>learning_rate：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].learning_rate" :min="0.01"
            :step="0.01"></el-input-number>
        </div>
        <!-- random_state -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>random_state：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].random_state" :min="0"
            :step="0.01"></el-input-number>
        </div>
        <!-- max_depth -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>max_depth：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].max_depth" :min="1" :step="1"></el-input-number>
        </div>
        <!-- n_estimators -->
        <div class="setting-method" style="margin-right: 20px;">
          <span>n_estimators：</span>
          <el-input-number v-model="train_algorithm_lgb_setting.paras[0].n_estimators" :min="1"
            :step="1"></el-input-number>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>参数说明</b>
      </div>
      <div style="margin-bottom: 20px;">
        <el-collapse>
          <el-collapse-item title="测试集占比">
            训练模型时会将内置数据拆分为两部分，一部分是训练集，用来训练模型，一部分是测试集，用来测试训练的模型的效果。测试集占比就是测试集的数据量占全部内置数据的比值
          </el-collapse-item>
          <el-collapse-item title="learning_rate">
            学习率
          </el-collapse-item>
          <el-collapse-item title="random_state">
            随机数种子
          </el-collapse-item>
          <el-collapse-item title="max_depth">
            对于基础学习器的最大树深度
          </el-collapse-item>
          <el-collapse-item title="n_estimators">
            增强型树的数量
          </el-collapse-item>
        </el-collapse>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>算法说明</b>
      </div>
      <p>
        LightGBM属于boosting算法中的一种，是GBDT算法的一个工程实现。LightGBM在算法结构上作了以下优化，使它的速度更快、效率更高：通过直方图算法降低分裂点的数量，通过基于梯度的单边采样技术降低样本的数量，通过互斥的特征捆绑算法减少了特征的数量。除此之外，在树的生长策略上，通过叶子生长策略提高了模型的精度。另外，LightGBM支持类别特征，并且采用多对多的切分方式将类别特征分为2个子集，实现类别特征的最优切分。最后，LightGBM还进行了工程上的优化，通过特征并行、数据并行和投票并行的方式实现高效并行，通过直方图算法对Cache的命中率进行优化。
      </p>
      <p>
        LightGBM的目标函数可表示为：
      </p>
      <p>
        <Latex>\hat{y} = {\sum\limits_{k = 1}^{K}{f_{k}(x)}}，f_{k} \in F</Latex>
      </p>
      <p>
        K——决策树的棵树；
      </p>
      <p>
        F——对应所有决策树的集合；
      </p>
      <p>
        f(k)——第k次迭代产生的第k棵决策树，为函数空间F中的函数；
      </p>
      <p>
        树的复杂度表示为树的叶子节点个数T和每棵树叶子节点输出结果w的平方和：
      </p>
      <p>
        <Latex>\Omega\left( f_{t} \right) = \gamma T + \frac{1}{2}\lambda{\sum\limits_{j = 1}^{t}w_{j}^{2}}</Latex>
      </p>
      <p>
        LightGBM的损失函数可表示为：
      </p>
      <p>
        <Latex>L^{t}(\varphi) = {\sum\limits_{i = 1}^{N}{l\left( {y_{i},{\hat{y}}_{t}} \right)}} + {\sum\limits_{j =
          1}^{t}{\Omega\left( f_{i} \right)}} = {\sum\limits_{i = 1}^{N}\left( {g_{i}f_{t}\left( x_{i} \right) +
          \frac{1}{2}h_{i}f_{t}^{2}\left( x_{i} \right)} \right)} + \gamma T + \frac{1}{2}\lambda{\sum\limits_{j =
          1}^{t}w_{j}^{2}}</Latex>
      </p>
      <p>
        对每个叶子节点进行计算，设函数q(x)表示样本x对应的叶子节点，则：
      </p>
      <p>
        <Latex>f_{t}(x) = w_{q}(x)</Latex>
      </p>
      <p>
        对损失函数进行变形可得：
      </p>
      <p>
        <Latex>L^{t}(\varphi) = {\sum\limits_{j = 1}^{T}\left( {G_{i}w_{i} + \frac{1}{2}\left( {H_{j} + \lambda}
          \right)w_{j}^{2}} \right)} + \gamma T</Latex>
      </p>
      <p>
        其中<Latex>G_{j} = {\sum\limits_{i \in I_{j}}g_{i}}</Latex>，<Latex>H_{j} = {\sum\limits_{i \in I_{j}}h_{i}}</Latex>
        ，需要对目标函数进行优化，即计算第t轮时使目标函数最小的叶节点的输出分数w(j)，直接对w(j)求导，使导数为0，得到使损失函数最小的
        <Latex>w_{j} = - \frac{G_{j}}{H_{j} + \lambda}</Latex>，将其代入损失函数得：
      </p>
      <p>
        <Latex>L^{t}(\varphi) = {\sum\limits_{j = 1}^{T}\left( {- \frac{1}{2}\frac{G_{j}^{2}}{H_{j} + \lambda}} \right)} +
          \gamma T</Latex>
      </p>
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model">训练模型</el-button>
          <el-button type="primary" @click="save">保存模型</el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_lgb_result).length > 0" class="model-result">
        <!-- 误差统计 -->
        <b>
          BSR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lgb_result).length > 0" :data="[train_algorithm_lgb_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR：
        </b>
        <el-table v-if="Object.keys(train_algorithm_lgb_result).length > 0" :data="[train_algorithm_lgb_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!-- 折线图 -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import { draw_lineplot } from "@/utils/draw";
import Latex from "@/components/Latex.vue";



export default {
  name: 'TrainAlgorithmLgb',
  components: {
    Latex
  },
  data() {
    return {
      // 表头
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_lgb_setting: { // LightGBM回归参数
      get() {
        return this.$store.state.train_algorithm_lgb_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lgb_setting', newVal);
      }
    },
    train_algorithm_lgb_result: { // LightGBM回归训练结果
      get() {
        return this.$store.state.train_algorithm_lgb_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lgb_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新train_algorithm_lgb_setting
    train_algorithm_lgb_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_lgb_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 训练模型
    train_model() {
      // 没有处理过数据时警告
      if (this.train_process_result.length > 0) {
        // 发送请求
        this.train_algorithm_lgb_setting.data = this.train_process_result;
        train(this.train_algorithm_lgb_setting).then(result => {
          this.train_algorithm_lgb_result = result;
          // 绘制折线图
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先处理内置数据！',
          type: 'error'
        });
      }
    },
    // 保存训练的模型
    save() {
      // 没有训练过即没有可以保存的模型时警告
      if (Object.keys(this.train_algorithm_lgb_result).length > 0) {
        const train_algorithm_lgb_setting = this.train_algorithm_lgb_setting;
        const train_algorithm_lgb_result = this.train_algorithm_lgb_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_lgb_setting,
          train_algorithm_result: train_algorithm_lgb_result
        };
        // 保存成功通知
        this.$message({
          message: '保存模型成功',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '请先训练模型！',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 绘制折线图
      draw_lineplot("lr-bsr-lineplot", this.train_algorithm_lgb_result, "bsr");
      draw_lineplot("lr-sbr-lineplot", this.train_algorithm_lgb_result, "sbr");
      draw_lineplot("lr-d-lineplot", this.train_algorithm_lgb_result, "d");
    },
  },
  mounted() {
    // 绘制折线图
    if (Object.keys(this.train_algorithm_lgb_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-lgb {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}</style>
